<template>
<div>
 <div class="portal-bg">
   <div class="portal-top">
     <p class="title">欢迎使用百讯物联网设备接入平台
     <a-button style="float: right;" @click="goBack">关闭</a-button>
     </p>
     <p class="description">物联网设备接入平台（IoTDm）是百讯智汇科技的物联网平台，提供海量设备连接上云、设备和平台双向消息通信、远程控制、场景规则等能力，并可将设备数据灵活流转到其他服务</p>
   </div>
   <div class="portal-content">
      <div class="portal-scenario">
         <div class="scenario-top">
           <p>设备数据流转</p>
         </div>
        <div class="content-align">
          <div class="content-inner">
            <p>您可以仅使用设备接入能力，平台将设备数据直接转发至其他云服务器处理</p>
            <a-radio-group v-model="size" style="margin-bottom: 16px">
              <a-radio-button value="platform">
                平台侧
              </a-radio-button>
              <a-radio-button value="equipment">
                设备侧
              </a-radio-button>
              <a-radio-button value="application">
                应用侧
              </a-radio-button>
            </a-radio-group>
            <div v-if="size === 'platform'">
              <div class="content-inserted">
                <div class="inserted-img">
                  <img src="../../assets/images/home/IoT.png" alt="" width="100%">
                </div>
                <div class="inserted-text">
                  <div class="text-item">
                    <div class="text-img">
                      <img src="../../assets/images/home/home01.png" alt="">
                    </div>
                    <div class="text-content">
                      <p class="titles">创建产品</p>
                      <p class="description-title">用于平台识别设备，同一类设备归属一个产品。</p>
                    </div>
                  </div>
                  <div class="text-item">
                    <div class="text-img">
                      <img src="../../assets/images/home/home02.png" alt="">
                    </div>
                    <div class="text-content">
                      <p class="titles">注册设备</p>
                      <p class="description-title">为设备在平台上创建对应的“身份”，注册设备ID及鉴权信息可以保证连接的安全性</p>
                    </div>
                  </div>
                  <div class="text-item">
                    <div class="text-img">
                      <img src="../../assets/images/home/home03.png" alt="">
                    </div>
                    <div class="text-content">
                      <p class="titles">配置数据处理</p>
                      <p class="description-title">添加处理设备数据服务资源，比如HTTP或Rabbit,然后在平台添加数据转发规则，指定数据流转到前述服务</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div v-if="size === 'equipment'">
              <div class="content-inserted">
                <div class="inserted-img">
                  <img src="../../assets/images/home/equipment.png" alt="" width="100%">
                </div>
                <div class="inserted-text">
                  <div class="text-item">
                    <div class="text-img">
                      <img src="../../assets/images/home/home01.png" alt="">
                    </div>
                    <div class="text-content">
                      <p class="titles">端测集成</p>
                      <p class="description-title">平台预集成各类主流芯片/模组/开发板以及丰富的开发资源，方便您选择适合的集成方式。 </p>
                    </div>
                  </div>
                  <div class="text-item">
                    <div class="text-img">
                      <img src="../../assets/images/home/home02.png" alt="">
                    </div>
                    <div class="text-content">
                      <p class="titles">设备接入</p>
                      <p class="description-title">设备注册后，将真实设备上电完成接入鉴权，与平台进行连接 </p>
                      <p class="description-title">设备接入平台后，您可以在设备详情中查看设备状态，监测设备和平台之间通信是否正常。</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div v-if="size === 'application'">
              <div class="content-inserted">
                <div class="inserted-img">
                  <img src="../../assets/images/home/platform.png" alt="" width="100%">
                </div>
                <div class="inserted-text">
                  <div class="text-item">
                    <div class="text-img">
                      <img src="../../assets/images/home/home01.png" alt="">
                    </div>
                    <div class="text-content">
                      <p class="titles">应用配置</p>
                      <p class="description-title">应用侧集成数据处理服务（如HTTP或Rabbit），从消息队列中获取设备上报的数据。</p>
                    </div>
                  </div>
                  <div class="text-item">
                    <div class="text-img">
                      <img src="../../assets/images/home/home02.png" alt="">
                    </div>
                    <div class="text-content">
                      <p class="titles">应用的接入</p>
                      <p class="description-title">应用服务器通过订阅推送数据，告知平台消息推送的地址和通知类型。</p>
                      <p class="description-title">应用接入平台并订阅数据后需验证，能否从进行设备数据处理的的云服务里成功获取数据。</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
   </div>

 </div>
</div>
</template>

<script>
export default {
  name: 'dmPortal',
  data() {
    return {
      size: 'platform'
    }
  },
  methods: {
    goBack() {
      this.$router.push('/home')
    }
  }
}
</script>

<style lang="scss" scoped>
.portal-bg {
  padding: 0 20px 20px 0;
  margin: 24px;
  .portal-top {
    position: relative;
    height: 12.5rem;
    padding: 2rem;
    background: #fff;
    background-image: url("../../assets/images/home/homeBg.png");
    background-size: 100% auto;
    .title {
      font-size: 1rem;
      font-weight: 700;
    }
  }
  .portal-content {
    height: 100%;
    box-shadow: 0 1px 3px rgba(2, 25, 42, 0.1);
    background: #fff;
    .portal-scenario {
      position: relative;
      z-index: 100;
      margin: -5.12rem 2rem 0;
      background: #fff;
      padding: 20px;
      .scenario-top {
        font-size: .875rem;
        font-weight: 600;
        color: #252b3a;
        line-height: 1.25rem;
        margin-bottom: 0.8rem;
      }
    }
    .content-align {
      width: 100%;
      display: flex;
      justify-content: center;
      .content-inner {
        width: 70rem;
        text-align: center;
        .content-inserted {
          padding: 0 1.25rem 4rem 0;
          display: flex;
          width: 100%;
          justify-content: space-between;
          .inserted-img {
            width: 49%;
            vertical-align: top;
            margin-right: 3rem;
          }
          .inserted-text {
            vertical-align: top;
            width: 45.8%;
            padding-top: 1rem;
            .text-item {
              display: flex;
              border-bottom: 1px dotted #dfe1e6;
              .text-img {
                img {
                  width: 100px;
                  height: 100px;
                }
              }
              .text-content {
                margin-left: 1.5rem;
                margin-top: 1rem;
                margin-bottom: 1rem;
                .titles {
                  font-size: 14px;
                  font-weight: 700;
                  margin-bottom: 0.5rem;
                  text-align: left;
                }
                .description-title {
                  font-size: 12px;
                  color: #8a8e99;
                  letter-spacing: 0;
                  line-height: 1.125rem;
                  text-align: left;
                }
                p {
                  margin-bottom: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>



